import { useRef, useState } from 'react'

const Slider = ({ imageId, images, handleZoomClose }) => {
  const [currentImage, changeImage] = useState(imageId)
  const [zoomImage, setZoomImage] = useState(
    {
      image: 'slider_image',
      button: 'slider_button-zoom-close'
    }
  )
  const [positionZoom, setPositionZoom] = useState(
    {
      top: 0,
      left: 0,
      x: 0,
      y: 0,
      mouseDown: false,
      buttonMoveStyle: {
        transform: 'translateX(0px) translateY(0px)'
      }
    }
  )
  const imageReference = useRef()

  // Move Images:
  const slideToLeft = () => {
    if ((currentImage - 1) === -1) {
      changeImage(images.length - 1)
      setZoomImage(
        {
          image: 'slider_image',
          button: 'slider_button-zoom-close'
        }
      )
    } else {
      changeImage(currentImage - 1)
      setZoomImage(
        {
          image: 'slider_image',
          button: 'slider_button-zoom-close'
        }
      )
    }
  }
  const slideToRight = () => {
    if (currentImage === images.length - 1) {
      changeImage(0)
      setZoomImage(
        {
          image: 'slider_image',
          button: 'slider_button-zoom-close'
        }
      )
    } else {
      changeImage(currentImage + 1)
      setZoomImage(
        {
          image: 'slider_image',
          button: 'slider_button-zoom-close'
        }
      )
    }
  }
  const handleKeyUp = (e) => {
    const key = e.keyCode
    if (key === 37) {
      slideToLeft()
    } else if (key === 39) {
      slideToRight()
    }
  }

  // Zoom:
  const handleZoomImageOpen = () => {
    setZoomImage(
      {
        image: 'slider_image-zoom',
        button: 'slider_button-zoom-close-showed'
      }
    )
  }
  const handleZoomImageClose = (e) => {
    console.log('cerrar zoom')
    e.preventDefault()
    e.stopPropagation()
    setZoomImage(
      {
        image: 'slider_image',
        button: 'slider_button-zoom-close'
      }
    )
    imageReference.current.style.cursor = 'zoom-in'
  }

  // Move Image:
  let dx
  let dy
  const handleMouseDown = (e) => {
    if (zoomImage.image === 'slider_image-zoom') {
      imageReference.current.style.cursor = 'grabbing'
      setPositionZoom(
        {
          top: imageReference.current.scrollTop,
          left: imageReference.current.scrollLeft,
          // Get the current mouse position
          x: e.clientX,
          y: e.clientY,
          mouseDown: true,
          buttonMoveStyle: {
            transform: 'translateX(0px) translateY(0px)'
          }
        }
      )
    }
  }
  const handleMouseMove = (e) => {
    e.preventDefault()
    if (positionZoom.mouseDown) {
      // How far the mouse has been moved
      dx = e.clientX - positionZoom.x
      dy = e.clientY - positionZoom.y

      // Scroll the element
      imageReference.current.scrollTop = positionZoom.top - dy
      imageReference.current.scrollLeft = positionZoom.left - dx
      setPositionZoom(
        {
          top: imageReference.current.scrollTop,
          left: imageReference.current.scrollLeft,
          // Get the current mouse position
          x: e.clientX,
          y: e.clientY,
          mouseDown: true,
          buttonMoveStyle: {
            transform: `translateX(${positionZoom.left}px) translateY(${positionZoom.top}px)`
          }
        }
      )
    }
  }
  const handleMouseUp = (e) => {
    e.preventDefault()
    setPositionZoom(
      {
        top: 0,
        left: 0,
        // Get the current mouse position
        x: 0,
        y: 0,
        mouseDown: false,
        buttonMoveStyle: {
          transform: `translateX(${positionZoom.left}px) translateY(${positionZoom.top}px)`
        }
      }
    )
    imageReference.current.style.cursor = 'grab'
    imageReference.current.style.removeProperty('user-select')
  }

  return (
    <div className='slider' onKeyUp={handleKeyUp}>
      <button className='slider_button-left' title='ImÃ¡gen Anterior' onClick={slideToLeft}>
        <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 87.77 165.78'>
          <title>Left</title>
          <g id='Capa_2' data-name='Capa 2'>
            <g id='Capa_1-2' data-name='Capa 1'>
              <path d='M12,82.89,86.31,8.54a5,5,0,0,0,0-7.08h0a5,5,0,0,0-7.07,0L1.46,79.25a5.28,5.28,0,0,0,0,7.29l77.78,77.78a5,5,0,1,0,7.07-7.07Z' />
            </g>
          </g>
        </svg>
      </button>

      <div
        className='slider_container'
        onMouseDown={handleMouseDown}
        onMouseMove={handleMouseMove}
        onMouseUpCapture={handleMouseUp}
      >
        <div
          className='slider_image_container'
          ref={imageReference}
        >
          <img
            className={zoomImage.image}
            src={images[currentImage].path}
            draggable='false'
            onClick={handleZoomImageOpen}
          />
        </div>

        <button
          className={zoomImage.button}
          onClickCapture={handleZoomImageClose}
        >
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 87.78 87.78'>
            <title>Recurso 4</title>
            <g id='Capa_2' data-name='Capa 2'>
              <rect x='38.89' y='-16.11' width='10' height='120' rx='5' transform='translate(-18.18 43.89) rotate(-45)' />
              <rect x='-16.11' y='38.89' width='120' height='10' rx='5' transform='translate(-18.18 43.89) rotate(-45)' />
            </g>
          </svg>
        </button>
        {/* <img className='image-cover' src='./images/meme.jpeg' draggable='true' /> */}
      </div>

      <button className='slider_button-right' autoFocus title='ImÃ¡gen Siguiente' onClick={slideToRight}>
        <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 87.77 165.78'>
          <title>Right</title>
          <g id='Capa_2' data-name='Capa 2'>
            <g id='Capa_1-2' data-name='Capa 1'>
              <path d='M75.82,82.89,1.46,8.54a5,5,0,0,1,0-7.08h0a5,5,0,0,1,7.08,0L86.32,79.25a5.31,5.31,0,0,1,0,7.29L8.54,164.32a5,5,0,0,1-7.08-7.07Z' />
            </g>
          </g>
        </svg>
      </button>

      <button className='slider_button-close' onClick={handleZoomClose}>
        <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 87.78 87.78'>
          <title>Recurso 4</title>
          <g id='Capa_2' data-name='Capa 2'>
            <rect x='38.89' y='-16.11' width='10' height='120' rx='5' transform='translate(-18.18 43.89) rotate(-45)' />
            <rect x='-16.11' y='38.89' width='120' height='10' rx='5' transform='translate(-18.18 43.89) rotate(-45)' />
          </g>
        </svg>
      </button>

      <span className='slider_counter'>{currentImage + 1} of {images.length}</span>
    </div>
  )
}

export default Slider
